* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,body {
  height: 100%;
}
body {
  background-color: #FFDF8C;
  padding: 45px;
  font-family: 'Rubik', sans-serif;
}
button {
  cursor: pointer;
}
ul[class] {
  list-style-type: none;
}
.btn {
  padding: 15px 20px; 
  border-radius: 30px;
  border: 1px solid;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all .3s;
  text-decoration: none;
  width: fit-content;
}
.btn-filled {
  background-color: #FE5F1E;
  border-color: #FE5F1E;
  color: #fff;
}
.btn-bordered {
  background-color: #fff;
  border-color: #FE5F1E;
  color: #FE5F1E;
}
.btn-dark {
  background-color: #282828;
  border-color: #282828;
  color: #fff;
}
.btn-disabled {
  background-color: #fff;
  border-color: #D3D3D3;
  color: #CACACA;
  font-weight: 400;
}

.app {
  background-color: #fff;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.03);
  border-radius: 10px;
  padding: 12px;
  max-width: 1340px;
  margin: 0 auto;
} 
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 38px;
  border-bottom: 1px solid #F7F7F7;
}
.header__logo {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.header__logo-img {
  margin-right: 17px;
  height: 38px;
  width: 38px;
}
.header__logo-title {

}
.header__logo-title p{
  color: #181818;
  font-weight: 800;
  font-size: 24px;
  line-height: 29px;
}
.header__logo-title span{
  color: #7B7B7B;
  font-size: 16px;
  line-height: 19px;
}
.header__cart {

}
.header__cart span {
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  padding: 3px 13px 3px 0;
  margin-right: 13px;
}
.header__cart svg {
  margin-right: 8px;
}
.header__cart i {
  font-style: normal;
}
.nav {
  padding: 38px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.categories {
  display: flex;
  align-items: center;
}
.categories__item {
  background-color: #F9F9F9;
  border-color: transparent;
  padding: 14px 30px;
  margin-right: 10px;
  border-color: transparent;
  transition: all .3s;
}
.categories__item:last-child {
  margin-right: 0;
}
.categories__item.active {
  background-color: #282828;
  border-color: #282828;
  color: #fff;
}

.sort {
  display: flex;
  position: relative;
}
.sort img {
 margin-right: 7px; 
 transition: all .3s;
 transform: rotate(180deg);
}
.sort.active img {
  transform: rotate(0);
}
.sort > span {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  display: inline-block;
  margin-right: 8px;
}
.sort > button {
  color: #FE5F1E;
  font-size: 14px;
  line-height: 17px;
  vertical-align: middle;
  background: none;
  border: none;
  position: relative;
}
.sort > button::before {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 1px;
  border-bottom: 1px dashed #FE5F1E;
  top: 100%;
}
.sort__dropdown {
  position: absolute;
  top: calc(100% + 10px);
  right: 0;
  background: #FFFFFF;
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
  border-radius: 10px;
  padding: 13px 0;

  animation: fade .3s;
}
.sort__dropdown ul {
  list-style-type: none;
}
.sort__dropdown li {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  padding: 10px 15px;
  cursor: pointer;
}
.sort__dropdown li.active {
  background: rgba(254, 95, 30, 0.05);
  color: #FE5F1E;
}
.catalog {
 padding: 0 38px 84px;
}
.catalog__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  margin-bottom: 35px;
}
.catalog__grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 35px;
}
.product {
  text-align: center;
}
.product__img {
  width: 95%;
}
.product__title {
  margin: 11px 0 22px 0;
  font-weight: 800;
  font-size: 20px;
  line-height: 24px;
}
.product__settings {
  background-color: #F3F3F3;
  border-radius: 10px;
  padding: 6px;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 17px;
  color: #2C2C2C;
}
.product__settings button {
  background-color: transparent;
  border: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  padding: 8px 0;
  border-radius: 5px;
  flex: 1;
}
.product__settings button.active {
  background-color: #fff;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
}
.product__settings button.disabled {
  opacity: 0.3;
  pointer-events: none;
}
.product__base {
  display: flex;
  margin-bottom: 6px;
}
.product__size {
  display: flex;
}
.product__total {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.product__price {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
}
.product__buy {

}
.product__buy:hover {
  background-color: #FE5F1E;
  color: #fff;
}
.product__buy:hover svg path{
  fill: #fff;
}
.product__buy:hover span {
  background-color: #fff;
  color: #FE5F1E;
}
.product__buy svg {
  margin-right: 8px;
}
.product__buy svg path {
  transition: all 0.3s;
}
.product__buy span {
  width: 22px;
  height: 22px;
  background-color: #FE5F1E;
  color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 13px;
  line-height: 16px;
  margin-left: 8px;
  transition: all .3s;
}
.cart {
  padding: 120px 38px 150px;
}
.cart--empty {
  text-align: center;
}
.cart__title {
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;

  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}
.cart--has-product .cart__title {
  justify-content: flex-start;
}
.cart--has-product {
  max-width: 896px;
  margin: 0 auto;
}
.cart__text {
  font-size: 18px;
  line-height: 26px;
  color: #777777;
}
.cart__title img {
  margin-left: 10px;
}
.cart__title svg {
  margin-right: 18px;
}
.cart__img {
  margin-top: 48px;
  margin-bottom: 74px;
  max-width: 300px;
}
.cart__go-back {
  margin: 0 auto;
}
.cart__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 60px;
}
.cart__reset {
  font-size: 16px;
  line-height: 19px;
  color: #B6B6B6;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
}
.cart__reset svg {
  margin-right: 7px;
}
.cart__list {

}
.cart__product {
  display: flex;
  align-items: center;
  margin-bottom: 60px;
}
.cart__thumb {
  width: 80px;
  height: 80px;
  margin-right: 15px;
}
.cart__info {
  margin-right: auto;
}
.cart__info p {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  margin-bottom: 3px;
}
.cart__info span {
  font-size: 18px;
  line-height: 22px;
  color: #8D8D8D;
}
.cart__qty {
  display: flex;
  align-items: center;
}
.cart__price {
  margin: 0 auto;
}
.cart__qty span {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  padding: 0 12px;
}
.cart__product button {
  width: 32px;
  height: 32px;
  font-size: 20px;
  line-height: 1;
  font-weight: 700;
  border: 2px solid;
  background-color: transparent;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cart__qty button {
  color: #EB5A1E;
  border-color: #EB5A1E;
}
.cart__decrement {

}
.cart__increment {
  
}
.cart__price {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
}
.cart__delete {
  color: #D7D7D7;
  border-color: #D7D7D7;
}
.cart__total {
  display: flex;
  justify-content: space-between;
  align-items: center;

  font-size: 22px;
  line-height: 27px;
}
.cart__total-qty {

}
.cart__amount b{
  color: #EB5A1E;
}
.cart__actions {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}
@media (max-width: 1440px) {
  .catalog__grid {
      grid-template-columns: 1fr 1fr 1fr;
      grid-gap: 45px;
  }
}
@media (max-width: 1200px) {
  .catalog__grid {
      grid-template-columns: 1fr 1fr;
      grid-gap: 60px;
  }
}
@media (max-width: 991px) {
  body {
      padding: 25px;
  }
  .catalog__grid {
      grid-gap: 35px;
  }
  .nav {
      padding: 20px;
  }
  .header {
      padding: 20px;
  }
  .catalog {
      padding: 0 20px 60px;
  }
}
@media (max-width: 767px) {
  .catalog__grid {
      grid-template-columns: 1fr;
  }
  .product {
      max-width: 450px;
      width: 100%;
      margin: 0 auto;
  }
}

@keyframes fade {
  from { opacity: 0; }
  to { opacity: 1; }
}